import logo from './res/logo.svg';
import './App.css';
import {MdAccessTime, MdArrowDropDown, MdCheckCircle, MdClose, MdDataUsage, MdMonetizationOn, MdSupervisorAccount} from 'react-icons/md';
import { useState } from 'react';


let LoginSignupElm,setLoginSignupElm;
let LoginSignupOffset,setLoginSignupOffset;
let LoginSignupOpacity,setLoginSignupOpacity;
let isLoggedIn,setIsLoggedIn;


let Header = props=>{

  let [dropped,setDropped] = useState(false);

  return (
    <div style={{
      background:"white",
      height:"8vh",
      position:"relative",
      display:"flex",
      
    }}>
      <img src={logo} alt={""} style={{
        height:"7vh",
        width:"7vh",
        marginTop:".5vh"
      }}/>
      <span style={
        {
          flex:7,
          display:"flex",
          alignItems:"center",
          fontSize:"3vmax",
          fontFamily:"quicksand",
          color:"#001569",
          marginLeft:"2vw"
          }}>
        <span>Doctor's Appointment</span>
      </span>
      <button style={
        {
          flex:2, 
          margin:"1vh 2vw",
          background:"#484fff",
          border:"none",
          borderRadius:"1vmax",
          fontSize:"1.8vmax",
          color:"white",
          display:isLoggedIn?'none':'block'
        }}
        onClick={()=>{
          openLoginSignup();
        }}
        >
        Login
      </button>
      <button style={
        {
          flex:2, 
          margin:"1vh 2vw",
          background:"#484fff",
          border:"none",
          borderRadius:"1vmax",
          fontSize:"1.8vmax",
          color:"white",
          display:isLoggedIn?'flex':'none',
          flexDirection:'column',
          alignItems:'center',
          position:'relative'
        }
      } 
        onClick={()=>{
          setDropped(dropped?false:true);
        }}
        onBlur={()=>{
          setDropped(false);
        }}
      >
        <div style={{ height:'100%', display:'flex', alignItems:'center' }}>{isLoggedIn} <MdArrowDropDown/></div>
        <div style={{
          opacity:1,
          position:'absolute',
          background:'white',
          top:'100%',
          width:'100%',
          boxShadow:'0px 3px 5px rgba(0,0,0,0.5)',
          display:dropped?'flex':'none',
          alignItems:'center',
          justifyContent:"center"
        }}>
          <button style={{
            padding:'1vmin',
            background:'white',
            border:'none',
            }}>Manage Appointments</button>
        </div>
      </button>
    </div>
  );

};

let Banner = props=>{

  return (
  <div className="banner">
    <span 
    style={{
      fontFamily:"prata",
      fontWeight:"bold",
      fontSize:"4vmax",
      color:'white',
      textAlign:'center',
      maxWidth:'70%'
    }}
    >
      Find your trusted, Family<br/>Doctor on our platform
    </span>
    <button style={{
      margin:'3vmin',
      marginTop:'7vmin',
      backgroundColor:"#213fff",
      padding:'2vmin 3vmin',
      fontSize:'4vmin',
      color:"white",
      borderRadius:'2vmin',
      border:"none"
    }}>
      Book an Appointment
    </button>
  </div>
  );
};

let Slogan = props=>{

  return (
    <div style={{
      fontFamily:"quicksand",
      fontSize:"3vmax",
      textAlign:"center",
      color:"#002277",
      width:"90vw",
      marginLeft:"5vw"
    }}>
      We care about your Health and Pocket<br/>
      <span style={{fontSize:"2.3vmax"}}>Get an appointment for your local trusted Doctor</span>
    </div>
  );
};

let WhyUs = props=>{

  return(
    <div style={{
      width:"90vw",
      margin:"3vh 5vw",
      padding:"1vmin",
      boxSizing:"border-box",
      background:"rgba(150,230,250,0.2)",
      borderRadius:'2vw'
    }}>
      <h1 style={{
        textAlign:'center',
        fontFamily:'cinzel'
      }}>Why us?</h1>
      <ul className="whyus_list" style={{
        listStyle:"none",
        textAlign:"center",
        padding:0,
        fontSize:'2.3vmax',
        fontFamily:'prata'
      }}>
        <li> <MdCheckCircle className='whyus_list_icn'/> Trusted Doctors</li>
        <li> <MdSupervisorAccount className='whyus_list_icn'/> Avoid Queue</li>
        <li> <MdAccessTime className='whyus_list_icn' /> Time Saving</li>
        <li> <MdDataUsage  className='whyus_list_icn' /> Ease of Usage</li>
        <li> <MdMonetizationOn  className='whyus_list_icn' /> Affordable</li>
      </ul>
    </div>
  );
};

let Footer = props=>{

  return (
  <div style={{
    background:"rgb(0 12 47)",
    color:'white',
    textAlign:'center',
    padding:'3vmax 0',
  }}>
    Designed and Developed by<br/>
    &copy; A3S Group
  </div>);
};

function openLoginSignup() {
  setLoginSignupOffset("0%");
  setLoginSignupOpacity(1);
}

function closeLoginSignup() {
  setLoginSignupOffset("-140%");
  setLoginSignupOpacity(0);
}

function login() {
  let uname = document.getElementById("login_email").value;
  setIsLoggedIn(uname);
}

let LoginElm = props=>{

  return(
    <div  className="LoginSignupElm">
      <h1>Login</h1>
      <input id="login_email" placeholder="Email"></input>
      <input id="login_pass" placeholder="Password"></input>

      <buton className="act_button"
        onClick={()=>{
          login();
          closeLoginSignup();
        }}>
        Login
      </buton>

      <button className="chg_button" onClick={()=>{ 
        setLoginSignupElm(SignUpElm);
       }}>Signup</button>
    </div>
  );
};

let SignUpElm = props=>{

  return(
    <div className="LoginSignupElm">
      <h1>Signup</h1>
      <input placeholder="Email"></input>
      <input placeholder="Password"></input>
      <input placeholder="Confirm Password"></input>

      <buton className="act_button"
        onClick={()=>{
          login();
          closeLoginSignup();
        }}
      >
        Login
      </buton>

      <button  className="chg_button" onClick={()=>{ 
        setLoginSignupElm(LoginElm);
       }}>Login</button>
    </div>
  );
};

let LoginPrompt = props=>{

  [LoginSignupElm,setLoginSignupElm] = useState(LoginElm);
  [LoginSignupOffset,setLoginSignupOffset] = useState("-140%");
  [LoginSignupOpacity,setLoginSignupOpacity] = useState(0);

  return (
    <div style={{
      background:"rgba(0,0,0,0.6)",
      height:"100%",
      width:"100%",
      position:"fixed",
      top:0, left:0,
      zIndex:3,
      display:"flex",
      alignItems:"center",
      justifyContent:"center",
      opacity:LoginSignupOpacity,
      transform:`translateY(${LoginSignupOffset})`,
      transition:"all 0.4s"
    }}>
        <div style={{
          width:"80vmin",
          background:"white",
          borderRadius:"1.5vmax",
          display:"flex",
          alignItems:"center",
          justifyContent:"center",
          position:"relative"
        }}>
        <button style={{
          position:"absolute",
          top:"3%",
          right:"3%",
          background:"none",
          border:"none",
          fontSize:"3vmax"
        }}
          onClick={()=>{
            closeLoginSignup();
          }}
        >
          <MdClose/>
        </button>
          {LoginSignupElm}
        </div>
    </div>
  );
}

function App() {  

  [isLoggedIn,setIsLoggedIn] = useState(false);

  let styles = {
    root:{
      background:'white',
      margin:0,
      padding:0
    }
  };
  return (
    <div style={styles.root}>
      <LoginPrompt/>
      <Header/>
      <Banner/>
      <Slogan/>
      <WhyUs/>
      <Footer/>
    </div>
  );
}

export default App;
